.body {
  position: relative;
  overflow: hidden;
  height: 100vh;
  width: 100vw;
  min-height: 700px;
}

.panel {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: -10px;
  background-color: #fafafa;
  width: 520px;
  border-radius: 10px;
}

.virtualAiLogo {
  height: 33.73px;
  width: auto;
  margin-right: auto;
  margin-left: auto;
  margin-top: 50px !important;
  margin-bottom: 40px !important;
}

.title {
  color: #6d6d73;
  font-weight: 300;
  font-size: 25px;
  line-height: 35.8px;
}

.description,
.errorAlertMessage {
  font-weight: 400;
  font-size: 14.3px;
  line-height: 17.9px;
  width: 390px !important;
  margin-left: 55px;
  margin-top: 20px !important;
  color: #6d6d73;
  text-align: left;
}

.errorAlertMessage {
  margin-top: 10px !important;
  margin-left: 65px;
  font-size: 13px;
  color: #ff7878;
}

.emailInput,
.passwordInput {
  font-size: 14px;
  line-height: 16.71px;
  font-weight: 400;
  color: #6d6d73;
  background: #fafafa;
  height: 33px;
  padding: 8px 15px 8px 5px;
  margin-top: 30px;
  box-shadow: inset 0px -1px 0px #6d6d73;
  border: none;
  outline: none;
}

.emailInput {
  margin-top: 30px !important;
  margin-left: 62px;
  width: 389px !important;
}

.passwordInput {
  margin-top: 20px;
  margin-left: 50px;
  width: 390px;
}

.eyeIcon {
  position: absolute;
  margin-top: 25px;
  margin-right: 70px;
  right: 0;
}

.button {
  margin-bottom: 50px;
  font-size: 18px;
  font-weight: 100;
  line-height: 21px;
  color: #ffffff;
  background-color: #008662;
  width: 390px !important;
  height: 43px;
  padding: 11px 24px;
  border-radius: 4px;
  border: none;
}

.passworthStrengthBarDiv {
  width: 390px;
  margin-left: 50px;
  margin-top: 5px;
}

.button:disabled {
  opacity: 0.8;
}
